

<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <TgList IsShowAction="false"
            ModuleName="CRM" 
            SubModuleCode="CRM_BRANDS" 
            IdentityColumn="brand_id"
            :HeaderText="$t('Brand')"
            :ListData="BrandData" 
            :HeaderData="Headers"
            :RenderRowActionMethod="renderActions"
            :HeaderButtons="listheaderbuttons" 
            :TotalRecords="TotalRecords"
            :SearchFields="leftSearchFields"
            :ListDataCallBackFunction="FetchData"
            :IsFilterApplied="searchCondition?true:false"
            :SortExp="SortExp"
            :SortBy="SortBy"
            :CurrentPageNumber="PageNumber"
            v-on:LeftsearchButtonClick="leftCommonSearch"
            v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick"
            v-on:SortdataButtonClick="sortdata">
        <template v-slot:slotdata="{data}">
            <template v-if="data.column.COLUMN_NAME == 'Thumbnail'">
                <td><img src="/Content/images/imagenotavailable.jpg" width="100" height="50" alt="Image"></td>
            </template>
            <template v-if="data.column.COLUMN_NAME == 'Social'">
                <div class="text-center SocialMedia_c" >
                    <RouterLink to="/Brand/SocialConnect" class="btn btn-success text-white">{{$t('Connect')}}</RouterLink>              
                </div>
            </template>
        </template>
    </TgList>
        <modaladdbrand 
            submodulecode="CRM_BRANDS"
            modulename="CRM"            
            v-if="showAddEditModal"        
            :modalAddEditItem="modalAddEditItem"   
            @close="ClosePopUp">
            <h5 slot="header">{{$t('Brand')}}</h5>
        </modaladdbrand>      
    </section>
</template>

<script>
    import Schema from "./BrandSchema"
    import BrandDataService from "../../../services/DataService"  
    import TgList from '../../../components/Common/TgList.vue'   
   import modaladdbrand from '../../../views/CRM/Brand/Model/Brand-add-model.vue'
export default {
        components: {
        modaladdbrand,
        TgList
    },
    data(){
        return{
            leftSearchFields: Schema.leftSearchFields,
            listheaderbuttons: Schema.buttons,
            modalAddEditItem:null,
            userid:null,
            usertype:null,
            PageNumber: 1,
            PageSize: 10,
            SortBy: '',
            SortExp: '',
            noRecord: false,
            TotalRecords: 0,
            searchCondition: '',
            isLoading:false,
            srchcondition:'',
            BrandData:null,
            showAddEditModal: false,
            fieldInfo:[],
            Headers:[
                // {
                //         "COLUMN_NAME": "Thumbnail",
                //         "DATA_TYPE": "",
                //         "SORTABLE": false,
                //         "VISIBLE": true,
                //         "DISPLAY_NAME": "Thumbnail",
                //         "DISPLAY_ORDER": 1
                //     },
                    {
                        "COLUMN_NAME": "brand_name",
                        "DATA_TYPE": "text",
                        "SORTABLE": true,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "brand_name",
                        "DISPLAY_ORDER": 1
                    },
                    {
                        "COLUMN_NAME": "brand_description",
                        "DATA_TYPE": "text",
                        "SORTABLE": true,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "brand_description",
                        "DISPLAY_ORDER": 2
                    },
                    {
                        "COLUMN_NAME": "created_at",
                        "DATA_TYPE": "date",
                        "SORTABLE": false,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "Created at",
                        "DISPLAY_ORDER": 3
                    },
                    {
                        "COLUMN_NAME": "Name",
                        "DATA_TYPE": "text",
                        "SORTABLE": false,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "Created by",
                        "DISPLAY_ORDER": 4
                    },
                    {
                        "COLUMN_NAME": "Social",
                        "DATA_TYPE": "text",
                        "SORTABLE": false,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "Social",
                        "DISPLAY_ORDER": 5
                    },
                ]
        }
    },    
    created: function () {
     
           
            this.userid = this.GetUserInfo.ID;
            this.usertype = this.GetUserInfo.UserType;
            var VueObj=this;
            VueObj.FetchData();
        },
    methods:{
        clickEventOfColumn:function(row, column){
            if(column.COLUMN_NAME == 'brand_name'){
                this.showAddEditModal = true;
                this.modalAddEditItem = row;
            }
        },
        renderActions: function(row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = Object.assign(actionItem);
                switch (actionItem.title) {
                    case 'View':
                       action.isVisible=row.status_id!='Draft';
                       break;
                    case 'Edit':
                        action.isVisible = row.ref_source_id<=0 && row.status_id != 'Closed';
                        break;
                    case 'FollowUps':
                        action.isVisible = row.status_id != 'Closed';
                        break;
                    case 'ConvertSalesOrder':
                        action.isVisible = row.status_id!='Draft' && row.ref_source_id<=0 && row.status_id!='Closed';
                        break;                                               
                }
                rtnActionArray.push({...action});
            });
            return rtnActionArray;
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchCondition;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        openPopUp: function () {
                this.showAddEditModal = true;
        },
        ClosePopUp: function () {
            this.showAddEditModal = false; 
            this.modalAddEditItem = null;
            this.FetchData();
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) { 
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
                    case "openPopUp":
                        this.openPopUp();
                        break;
                        case "setTourGuideSteps":
                    this.setTourGuideSteps(0,this);
                        break;
            }
        },
        commonDeleteData: function (event) {
            var vm = this;
                    var val = "";
                    $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                        if (val.length > 0)
                            val += ',';
                        val += $(this).val();
                    });           
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " +vm.$t("Brand")), function () {
                    var postJSON = {
                        ids: val,
                        moduleName: "CRM",
                        subModuleCode: "CRM_BRANDS"
                    };
                    var objectPostString = JSON.stringify(postJSON);
                   BrandDataService.CommonDeleteData(objectPostString).then(function (response) {                                
                        var box = "";
                        if (response.status == 200) {
                            $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                         $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                            $("#DeleteMultiple").addClass("disabled");
                            box= vm.ShowAlert(vm.$t('DeletedSuccessfully', [vm.$t('Brand')]), "success", true, vm.$t("Alert"));
                            vm.PageNumber=parseInt(vm.PageNumber)-1;
                            if(vm.PageNumber<0){
                                vm.PageNumber=1;
                            } 
                            vm.FetchData(); 
                        }
                        else {
                             box= vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Brand')]), "failure", true, vm.$t("Alert"));
                        }
                        setTimeout(function () {
                            box.modal('hide');
                        }, 2000);
                         
                    }, );
                });
            }
            else {
                alert(vm.$t("Selectanyrecordtodelete"));
            }                
        },
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
                if (s === this.SortBy) {                    
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else
                {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }               
            this.FetchData();
            $("#" + s+'  span:first-child').attr("class", SortDir);
        },
        pagerMethod: function (value) {             
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        getRequestParams: function (pageNum, pageSize, sortBy, sortExp, isPaged, brandname) {
            let params = {};
            if (pageNum) { params["pageNum"] = pageNum; }
            if (brandname) { params["brandname"] = brandname; }
            if (isPaged) { params["isPaged"] = isPaged; }
            if (sortBy) { params["sortBy"] = sortBy; }
            if (sortExp) { params["sortExp"] = sortExp; }
            if (pageSize) { params["pageSize"] = pageSize; }
            return params;
        },
        FetchData: function(){ 
            const params = this.getRequestParams(this.pageNumber, this.pageSize, this.sortBy, this.sortExp, this.isPaged, this.brandname);                        
            var VueObj = this;
            VueObj.isLoading = true;                             
            params.pageSize = VueObj.PageSize;
            params.pageNumber = VueObj.PageNumber;
            params.brandname = VueObj.leftSearchFields[0].value;
            if (VueObj.PageNumber) { params.pageNum = VueObj.PageNumber; }
            else { VueObj.PageNumber = params.pageNum = 1; }
            if (typeof VueObj.isPaged != 'undefined') { params.isPaged = VueObj.isPaged; }
            else { VueObj.isPaged = params.isPaged = 1; }
            params.isPaged = VueObj.isPaged;
            params.sortBy = VueObj.SortBy;
            params.sortExp = VueObj.SortExp; 
            BrandDataService.BrandListing(params).then(function(response){
                VueObj.isLoading=false;
                VueObj.Headers.forEach((column) => {
                    column.settings = null;
                    if(column.COLUMN_NAME == 'brand_name'){
                        column.settings = {
                            clickEvent: VueObj.clickEventOfColumn,
                            formatter: VueObj.columnDataFormatter
                        }
                    }
                    if(column.COLUMN_NAME == 'Social'){
                        column.settings = {
                            isInSlot : true
                        }
                    }
                    if(column.COLUMN_NAME == 'Thumbnail'){
                        column.settings = {
                            isInSlot : true
                        }
                    }
                });
                if (response.data != null && response.data != "") {                   
                        VueObj.BrandData = response.data.data;
                        VueObj.TotalRecords = response.data.data[0].totalrecords;
                        VueObj.TotalPages = Math.ceil(VueObj.TotalRecords / VueObj.PageSize);
                        VueObj.CurrentPage = VueObj.PageNumber;
                        $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                        $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                        VueObj.noRecord = false;
                    }
                else 
                {
                        VueObj.noRecord = true;
                        VueObj.BrandData = [];
                        VueObj.TotalRecords = 0;
                    }
                    setTimeout(function(){
                                VueObj.ResponsiveDataTable('tablelistingdata')
                            }, 500);           
                            VueObj.isLoading = false;        
            });
        }
    }
}
</script>