<template>
    <transition name="modal">
        <div class="my-popups">
            <div class="modal d-block dialog">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content SocialMedia_b">
                        <div class="modal-header">
                            <slot name="headerForModal">
                                <h5 class="modal-title" v-if="AddEditText==='Edit'">{{$t('EditBrand')}}</h5>
                                <h5 class="modal-title" v-else>{{$t('AddBrand')}}</h5>
                            </slot>
                            <button type="button" title="" v-on:click="$emit('close')" class="close" data-original-title="Close"><em class="fa fa-times" aria-hidden="true"></em></button>
                        </div>
                        <div class="modal-body" >
                            <Form ref="form" v-slot="{  valid,errors}">
                                <div class="col-md-12 p-0">
                                    <div class="row">
                                        <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left">
                                            <div class="form-group">
                                                <label>{{$t('BrandName')}}:<span class="text-danger">*</span></label>
                                                <input class="form-control" type="hidden" id="txtBrandId" name="txtBrandId" v-model.trim="txtBrandId" />
                                                <Field name="Brand Name" :rules="{'required':true}"  v-slot="{errors}">
                                                    <input  v-bind:class="{ 'form-control': true, 'is-invalid': errors[0]}" type="text"  maxlength="200"  v-model="txtAddBrand"  />
                                                    <span   name="Brand Name" class="invalid-feedback">{{errors[0]}}</span>
                                                </Field>                                                                                           
                                            </div>
                                        </div>
                                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left">
                                            <div class="form-group">
                                            <label>{{$t('Description')}}:</label>
                                            <Field name="txtDescription" :rules="{'required':false}" label="Description" v-slot="{errors,touched,invalid}">
                                            <textarea class="form-control" id="txtDescription" maxlength="1000" v-model="txtDescription" />
                                            <span   name="txtDescription" class="invalid-feedback">{{errors[0]}}</span>
                                                </Field>   
                                            </div>
                                        </div>
                                    </div>
                                    <div class="row flex-row-reverse">
                                        <div class="col-lg-6 text-right">
                                            <a href="javascript:;" v-if="AddEditText==='Edit'" :title="$t('Update')" v-on:click="SubmitBrand( valid,errors)" class="btn btn-success mr-1"><em class="fa fa-send pr-2"></em>{{$t('Update')}}</a>
                                            <a href="javascript:;" v-if="AddEditText!='Edit'" :title="$t('Save')" v-on:click="SubmitBrand( valid,errors)"  class="btn btn-success mr-1"><em class="fa fa-save pr-2"></em>{{$t('Save')}}</a>
                                            <a href="javascript:;" :title="$t('Close')" v-on:click="$emit('close')" class="btn btn-danger"><em class="fa fa-close pr-2"></em>{{$t('Close')}}</a>
                                        </div>
                                        <div class="col-lg-6">
                                            <div class="py-2">
                                                <div class="py-2"><small><span class="text-danger">
                                                         Fields marked with an asterisk (*) are mandatory.</span></small>
                                                 </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </Form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </transition>
</template>

<script>
 import LeadDataService from "../../../../services/DataService";
export default {
    props:{
        modalAddEditItem:null
    },
    data() {
            return {
                txtAddBrand: '',
                txtDescription: '',
                AddEditText: '',
                txtBrandId: 0,
                modal:null
                
            }
        },
        created: function () {
            this.modal = this.modalAddEditItem;
            if (this.modal != null) {
                this.FetchAddEditBrandData(this.modal.brand_id);
            }
        },
        
        methods:{
            FetchAddEditBrandData: function (id) {
                var vm=this;
                this.AddEditText = "Edit"; 
                LeadDataService.FetchAddEditBrandData(id).then(response => {
                    if (response.data.data != null) {
                        vm.txtBrandId = response.data.data[0].brand_id;
                        vm.txtAddBrand = response.data.data[0].brand_name;
                        vm.txtDescription = response.data.data[0].brand_description;
                        var frmFields={}
                     frmFields['txtAddBrand']=vm.txtAddBrand;
                     frmFields['txtDescription']=vm.txtDescription;
                    vm.$refs.form.resetForm({values:frmFields});
                    }
                });
            },
            SubmitBrand( valid,errors) { 
             
                var vm=this;   
                // vm.$refs.form.setFieldTouched('txtAddBrand', true);
                vm.$refs.form.validate().then((result) => {
                    vm.vSelectValidate = true;
                    if (result) {
                        var fullJson = {
                        brand_id: this.txtBrandId,
                        brandname: this.txtAddBrand,
                        branddescription: this.txtDescription,
                        STATUS_ID: 1001,
                    };
                    var postJSON = {
                        data: fullJson,
                    };
                    var objectPostString = JSON.stringify(postJSON);
                      LeadDataService.SaveBrand(objectPostString).then(function (response) {
                            if (response.data[0].Status == "Success") {
                            vm.$emit('close'); 
                            if (vm.AddEditText == "Edit") {                           
                                vm.ShowAlert(vm.$t('UpdatedSuccess', [vm.$t('Brand')]), "Success", true, vm.$t("Alert"));
                            }
                            else {                           
                                vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('Brand')]), "Success", true, vm.$t("Alert"));
                            }
                        }
                        else {
                            vm.$emit('close');
                             vm.ShowAlert(vm.$t('Error', [vm.$t('Brand')]), "Warning", true, vm.$t("Alert"));
                        }
                      })
                    }
                });
            }
        }
    }
</script>
